<template>
  <div class="min-h-screen bg-ext-light-pink">
    <AppHeader />
    <app-loader :show="loader" />
    <div class="absolute bottom-0 w-full lg:flex top-16">
      <div class="self-center flex-grow hidden lg:block">
        <TimeCapsulePreview />
      </div>
      <div
        class="flex-shrink bg-white relative xl:w-[512px] lg:w-[380px] sideBar"
      >
        <ul
          class="fixed z-10 grid w-full grid-cols-4 mb-6 text-sm font-semibold text-center bg-white lg:grid-cols-3 sm:text-base shadow-ext-bottom-2 lg:relative lg:w-auto"
        >
          <li
            class="py-2 cursor-pointer lg:hidden"
            :class="
              previewSelected
                ? 'border-b-2 border-ext-yellow bg-ext-yellow text-white'
                : 'bg-dark text-white'
            "
            @click="selectedTab = 'preview'"
          >
            <img
              src="../assets/images/eyeIcon.svg"
              alt=""
              class="mx-auto mb-2"
            />
            Preview*
          </li>
          <li
            class="py-2 cursor-pointer"
            :class="
              designSelected || lastActiveTab == 'design'
                ? 'border-b-2 border-black'
                : 'opacity-50'
            "
            @click="previewSelected && (selectedTab = lastActiveTab)"
          >
            <img
              :src="
                tabDesignValidated
                  ? require('../assets/images/CircleWavyCheckGreen.svg')
                  : require('../assets/images/tabDesign.svg')
              "
              alt=""
              class="mx-auto mb-1"
            />
            Design
          </li>
          <li
            class="py-2 cursor-pointer"
            :class="
              detailsSelected || lastActiveTab == 'details'
                ? 'border-b-2 border-black'
                : 'opacity-50'
            "
            @click="previewSelected && (selectedTab = lastActiveTab)"
          >
            <img
              :src="
                tabDetailsValidated
                  ? require('../assets/images/CircleWavyCheckGreen.svg')
                  : require('../assets/images/tabDetails.svg')
              "
              alt=""
              class="mx-auto mb-1"
            />
            Details
          </li>
          <li
            class="py-2 cursor-pointer"
            :class="
              formatSelected || lastActiveTab == 'format'
                ? 'border-b-2 border-black'
                : 'opacity-50'
            "
            @click="previewSelected && (selectedTab = lastActiveTab)"
          >
            <img
              src="../assets/images/tabFormat.svg"
              alt=""
              class="mx-auto mb-1"
            />
            Format
          </li>
        </ul>
        <div
          ref="options"
          class="absolute inset-x-0 bottom-0 pb-16 overflow-y-auto top-24"
          :class="previewSelected ? 'hidden' : ''"
        >
          <div v-show="designSelected">
            <frame-size />
            <location-input
              ref="locationInput"
              @update-center="
                (center) => {
                  if (center) {
                    updateLocation(center, 1);
                    locationAdded = true;
                  } else {
                    locationAdded = false;
                  }
                }
              "
            />
            <div
              v-show="mobileView"
              class="relative h-48 mx-5 mb-5 rounded-md shadow"
            >
              <map-component
                :api="getMapStyle[0].mapTileApi"
                :marker="getMapMarker[0]"
                :markerIndex="getMapMarker[0].index"
                :markerType="getMapMarker[0].type"
                :center="getCenter[0]"
                :index="0"
                showNav
                ref="mapComponent"
              />
            </div>
            <map-styling @update-style="(style) => updateMapStyle(style, 1)" />
            <map-marker
              @update-marker="(marker) => updateMapMarker(marker, 1)"
            />
          </div>
          <div v-show="detailsSelected">
            <div class="px-5 mb-8">
              <div class="mb-2 font-semibold">Title*</div>
              <input
                type="text"
                class="form-control"
                :class="focus == 'title' && title == '' ? 'errorInput' : ''"
                placeholder="Your title goes here"
                maxlength="40"
                v-model="title"
                @input="setImageTitle(title)"
              />
              <small class="text-xs text-ext-gray">Max 40 characters</small>
              <div class="pt-2">
                <input
                  id="af"
                  type="checkbox"
                  class="inputCheckSq"
                  v-model="alternativeFont"
                  @change="setAlternativeFont(alternativeFont)"
                />
                <label for="af">Alternative font</label>
              </div>
            </div>

            <div class="px-5 mb-8">
              <div class="mb-2 font-semibold">Subtext*</div>
              <input
                type="text"
                class="form-control"
                :class="
                  focus == 'subtitle' && subTitle == '' ? 'errorInput' : ''
                "
                placeholder="Subtext about your title"
                maxlength="40"
                v-model="subTitle"
                @input="setImageSubTitle(subTitle)"
              />
              <small class="text-xs text-ext-gray">Max 40 characters</small>
            </div>
          </div>
          <div v-show="formatSelected">
            <frame-color
              @update-frame-color="
                (color) => {
                  setFrameColor(color.color);
                  checkPrice('momentMap');
                }
              "
            />
            <printing-type
              @selected-printing-type="(type) => updatePrintType(type)"
            />
            <div class="px-5 mb-8" v-if="printType != 'Digital Copy'">
              <div class="mb-2 font-semibold">Gifting message</div>
              <input
                type="text"
                class="form-control"
                maxlength="100"
                placeholder="Write your message"
                v-model="giftingMessage"
                @change="setGiftingMessage(giftingMessage)"
              />
              <div class="my-2 text-xs text-ext-gray">
                Extra charges would apply for gifting
              </div>
              <input
                type="checkbox"
                class="mr-2 inputCheckSq"
                v-model="giftWrapping"
                @change="
                  setGiftWrapping(giftWrapping);
                  checkPrice('momentMap');
                "
              />
              Add gift wrapping
            </div>
          </div>
        </div>
        <div
          class="fixed inset-x-0 bottom-0 z-10 flex items-center justify-between p-2 bg-white shadow-sm sm:p-4 lg:absolute"
        >
          <span class="text-2xl font-semibold">
            <span v-if="formatSelected">₹ {{ getPrice }}</span>
          </span>
          <!-- <button @click.prevent="createImg">Capture</button> -->
          <div class="flex items-center">
            <button
              v-if="!designSelected && !previewSelected"
              class="mr-2 btn-outline"
              @click="back"
            >
              <img
                src="@/assets/images/ArrowRight.svg"
                alt="arrow"
                class="mr-1 rotate-180"
              />
              Previous
            </button>
            <button
              v-if="!previewSelected"
              class="btn-primary"
              :class="formatSelected && getPrice == 0 ? 'hidden' : ''"
              @click="next"
            >
              {{ ctaBtnText }}
              <img
                src="@/assets/images/ArrowRight.svg"
                alt="arrow"
                class="ml-1"
              />
            </button>
            <button
              v-else
              class="btn-primary"
              @click.prevent="selectedTab = 'design'"
            >
              Customize
              <img
                src="@/assets/images/ArrowRight.svg"
                alt="arrow"
                class="ml-1"
              />
            </button>
          </div>
        </div>
        <template v-if="previewSelected">
          <div class="self-center flex-grow block px-8 pt-24 lg:hidden">
            <TimeCapsulePreview />
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  // import { mapState } from "vuex";
  import FrameSize from '../components/productOptions/FrameSize.vue';
  import LocationInput from '../components/productOptions/LocationInput.vue';
  import AppHeader from '../components/AppHeader.vue';
  import AppLoader from '../components/AppLoader.vue';
  import MapStyling from '../components/productOptions/MapStyling.vue';
  import MapMarker from '../components/productOptions/MapMarker.vue';
  import FrameColor from '../components/productOptions/FrameColor.vue';
  import PrintingType from '../components/productOptions/PrintingType.vue';
  import TimeCapsulePreview from '../components/TimeCapsulePreview.vue';
  import MapComponent from '@/components/map/MapComponent.vue';
  import { mapGetters, mapMutations, mapActions } from 'vuex';

  export default {
    components: {
      AppHeader,
      AppLoader,
      FrameSize,
      LocationInput,
      MapStyling,
      MapMarker,
      FrameColor,
      PrintingType,
      TimeCapsulePreview,
      MapComponent,
    },
    data() {
      return {
        selectedTab: 'preview',
        title: '',
        subTitle: '',
        giftingMessage: '',
        giftWrapping: false,
        printType: null,
        price: null,
        alternativeFont: false,
        loader: false,
        locationAdded: false,
        tabDesignValidated: false,
        tabDetailsValidated: false,
        focus: '',
        lastActiveTab: 'design',
      };
    },
    computed: {
      ...mapGetters('momentMap', ['selectedFrameSize', 'selectedOption']),
      ...mapGetters('checkout', ['getPrice', 'getImages']),
      ...mapGetters('mapObj', [
        'mapDataUrl',
        'getMapMarker',
        'getMapStyle',
        'getCenter',
      ]),
      designSelected() {
        return this.selectedTab == 'design';
      },
      detailsSelected() {
        return this.selectedTab == 'details';
      },
      formatSelected() {
        return this.selectedTab == 'format';
      },
      previewSelected() {
        return this.selectedTab == 'preview';
      },
      mobileView() {
        return window.screen.width < 1024;
      },
      ctaBtnText() {
        return this.designSelected || this.detailsSelected
          ? 'Next'
          : 'Checkout';
      },
    },
    created() {
      this.mobileView
        ? (this.selectedTab = 'preview')
        : (this.selectedTab = 'design');
      this.resetMap();
      this.deleteCookies();
    },
    mounted() {
      this.deleteCookies();
    },
    methods: {
      ...mapMutations('momentMap', [
        'setImageTitle',
        'setImageSubTitle',
        'setGiftingMessage',
        'setGiftWrapping',
        'setFrameColor',
        'setPrintingType',
        'setAlternativeFont',
      ]),
      ...mapMutations('checkout', ['setProductDetails']),
      ...mapMutations('mapObj', [
        'setMapStyle',
        'setMapMarker',
        'setCenter',
        'resetMap',
      ]),
      ...mapActions('momentMap', ['createImg']),
      ...mapActions('checkout', ['checkPrice']),

      deleteCookies() {
        if (
          document.cookie.includes('csrftoken') ||
          document.cookie.includes('sessionid')
        ) {
          const date = new Date(
            new Date().setTime(new Date().getUTCSeconds() + 1),
          );
          document.cookie = `csrftoken=;expires=${date};`;
          document.cookie = `sessionid=;expires=${date};`;
        }
      },

      updateMapStyle(style, index) {
        this.deleteCookies();
        this.setMapStyle({
          name: style.name,
          api: style.mapTileApi,
          key: index,
        });
      },
      updateMapMarker(marker, index) {
        this.deleteCookies();
        this.setMapMarker({
          name: marker.name,
          index: marker.id,
          type: marker.type,
          image: marker.image,
          key: index,
        });
      },
      updateLocation(center, key) {
        this.deleteCookies();
        this.setCenter({ center: center, key: key });
      },
      updatePrintType(type) {
        this.deleteCookies();
        this.setPrintingType(type);
        this.printType = type.name;
        if (this.printType == 'Digital Copy') {
          this.giftWrapping = false;
          this.setGiftWrapping(this.giftWrapping);
          this.checkPrice('momentMap');
        } else {
          this.checkPrice('momentMap');
        }
      },
      back() {
        this.deleteCookies();
        if (this.designSelected) {
          this.selectedTab = 'preview';
        }
        if (this.detailsSelected) {
          this.selectedTab = 'design';
        }
        if (this.formatSelected) {
          this.selectedTab = 'details';
        }
      },
      async next() {
        this.deleteCookies();
        if (this.designSelected) {
          this.goToDetailsTab();
        } else if (this.detailsSelected) {
          this.goToFormatTab();
        } else if (this.formatSelected) {
          this.$refs.options.scrollTop = 0;
          this.loader = true;
          this.checkout();
          await this.createImg().then((x) => {
            this.loader = false;
            if (x) this.$router.push('/checkout');
          });
        }
      },
      goToDetailsTab() {
        this.deleteCookies();
        if (this.locationAdded) {
          this.$refs.options.scrollTop = 0;
          this.selectedTab = 'details';
          this.tabDesignValidated = true;
        } else {
          this.$refs.locationInput.makeFocus();
          this.$toast.open({
            message: 'Please add location.',
            type: 'error',
            duration: 5000,
            dismissible: true,
            position: 'top',
          });
          this.tabDesignValidated = false;
        }
      },
      goToFormatTab() {
        this.deleteCookies();
        if (this.title) {
          if (this.subTitle) {
            this.$refs.options.scrollTop = 0;
            this.selectedTab = 'format';
            this.tabDetailsValidated = true;
          } else {
            this.focus = 'subtitle';
            this.$toast.open({
              message: 'Please add your sub title.',
              type: 'error',
              duration: 5000,
              dismissible: true,
              position: 'top',
            });
            this.tabDetailsValidated = false;
          }
        } else {
          this.focus = 'title';
          this.$toast.open({
            message: 'Please add your title.',
            type: 'error',
            duration: 5000,
            dismissible: true,
            position: 'top',
          });
        }
      },
      checkout() {
        this.deleteCookies();
        this.setProductDetails({
          mapType: 1,
          productName: this.selectedOption.productName,
          frameSize: this.selectedOption.frameSize,
          frameColor: this.selectedOption.frameColor.color,
          printingType: this.selectedOption.printingType,
          giftingMsg: this.giftingMessage,
          giftWrap: this.giftWrapping,
        });
      },
    },
    watch: {
      formatSelected(newVal) {
        this.deleteCookies();
        if (newVal) {
          this.checkPrice('momentMap');
        }
      },
      designSelected(newVal) {
        this.deleteCookies();
        if (newVal == true) {
          this.$refs.mapComponent?.resizeMap();
        }
      },
      selectedFrameSize(val, oldVal) {
        this.deleteCookies();
        console.log(`new: ${val}, old: ${oldVal}`);
      },
      selectedTab(newVal) {
        this.deleteCookies();
        if (newVal != 'preview') {
          this.lastActiveTab = newVal;
        }
      },
    },
  };
</script>

<style scoped>
  .sideBar {
    height: calc(100vh - 65px);
  }
</style>
